.main_container{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border: 2px solid black;
    padding: 20px;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    border-radius: 12px;
}

.main_container input{
    padding: 10px;
    border: none;
    border-bottom: 2px solid black;
    font-size: 17px;
}
.main_container span{
    margin-top: 5px;
}
.enter-name{
    width: 100%;
    text-align: left;
    padding-left: 30px;
}
#next_name{
    padding: 10px;
    border: none;
    background-color: black;
    color: white;
    border-radius: 12px;
    cursor: pointer;
}

#next_name:hover{
    box-shadow: 2px;
}